
*,
*::before,
*::after {
  box-sizing: border-box;
}
body,
html {
  margin: 0;
  padding: 0;
}
body {
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #F1F4F9;
  color: #345A7D;
}
button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  font-family: inherit;
  cursor: pointer;
  outline: 0;
}
button:hover {
    box-shadow: 0 1px 20px -5px #000;
}
a {
  color: #147FED;
}
.wrapper {
  width: 1024px;
  margin: 0 auto;
}
.checkbox {
  position: relative;
  height: 20px;
  width: 20px;
}
.checkbox .input {
    position: absolute;
    top: -9999px;
    left: -9999px;
}
.checkbox .checkbox-style,
  .checkbox .label {
    position: absolute;
    top: 0;
    left: 0;
    width: 20px;
    height: 20px;
}
.checkbox .label {
    display: inline-block;
    z-index: 20;
    opacity: 0;
    overflow: hidden;
}
.checkbox .checkbox-style {
    z-index: 10;
    background: #FFF;
    border: solid 1px #C7D3DE;
    border-radius: 4px;
}
.checkbox .input:checked ~ .checkbox-style {
    border-color: #80C8F9;
}
.checkbox .input:checked ~ .checkbox-style::before {
      content: '';
      opacity: .5;
      width: 48px;
      height: 48px;
      position: absolute;
      top: -15px;
      left: -15px;
      transform: scale(0.4);
      background-image: url("~assets/icons/check.png");
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .2s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}



