
.table-wrapper {
  padding: 0 42px;
}
table {
  border-collapse: collapse;
  width: 100%;
  text-align: left;
  border-bottom: none;
}
table p {
    margin: 0;
}
tr {
  background: #FFF;
  border-bottom: solid 1px;
  border-left: solid 1px;
  border-right: solid 1px;
  border-color: #C7D3DE;
}
tr:first-child {
    border-top: solid 1px #C7D3DE;
    border-radius: 4px;
}
tr:hover {
    background: #FFFBEA;
}
tr.selected {
    background: #D5E9FC;
    border: solid 1px #80C8F9;
}
th {
  font-size: 12px;
  font-weight: 500;
  padding: 11px 15px;
  background: #f5f7fb;
}
td {
  font-size: 14px;
  padding: 14px 15px;
}
th:last-child,
td:last-child {
  text-align: right;
}
th:first-child,
td:first-child {
  padding-left: 14px;
}



